body{
    background-color: #29293d;
}

@font-face {
    font-family: "loading";
    src: url("../font/Tourney-SemiBold.ttf");
}

.container{
    width: 80%;
    margin: 0 auto;
    padding: 10px;
}

.loader{
    display: flex;
    justify-content : center;
    align-items : center;
    height: 100vh;
    font-family: "loading";
    font-size: 32px;
    font-weight: 800;
    color: white;
}

.movie_container{
    display: grid;
    grid-template-columns: 50% 50%;
}