.movie{
    margin: 30px 10px;
    background: linear-gradient(#f0f0f5, #a3a3c2, #52527a);
    /* border-radius: 6px; */
    top : 10px;
    display: grid;
    grid-template-columns: 40% 60%;
}

@font-face {
    font-family: "genre";
    src: url("../font/Knewave-Regular.ttf");
}

.movie_data{
    padding: 10px;
}

.movie_img{
    position: relative;
    top: -10px;
    left: -10px;
    border-radius: 10px;
}

.genres{
    padding: 0px;
}

.genre{
    list-style: none;
    font-family: "genre";
}

.detail_btn{
    background-color: transparent;
    border-radius: 10px;
    border: 1px solid black;
    cursor: pointer;
    background-color: #14141f;
    color: white;
}